import {
  strCountCharOccurances,
  strCountChars,
  strIsLowerCase,
  strIsUpperCase,
  strLinesRemoveEmpty,
  strLinesTrimLeft,
  strLinesTrimRight,
  strPrettifyMinifiedCode,
  strRemoveDuplicateChars,
  strSortChars,
  strSplitCamelCase,
  strToCharCodes,
  strToCharSet,
  strToSentences,
  strToWords,
  strUnwrap,
  strWrapBetween,
  strWrapIn,
  strWrapInAngleBrackets,
  strWrapInBraces,
  strWrapInBrackets,
  strWrapInDoubleQuotes,
  strWrapInParenthesis,
  strWrapInSingleQuotes,
} from '../'

/**
 * A String class extension with extra features.
 */
export class BemojeString extends String {
  static get [Symbol.species](): typeof String {
    return String
  }

  // eslint-disable-next-line @typescript-eslint/explicit-module-boundary-types
  constructor(thing?: any) {
    super(thing)
  }

  public get string(): string {
    return this.toString()
  }

  public get _(): string {
    return this.toString()
  }

  public countCharOccurances(char: string): number {
    return strCountCharOccurances(this._, char)
  }
  public countChars(): Map<string, number> {
    return strCountChars(this._)
  }
  public isLowerCase(): boolean {
    return strIsLowerCase(this._)
  }
  public isUpperCase(): boolean {
    return strIsUpperCase(this._)
  }
  public linesRemoveEmpty(): BemojeString {
    return new BemojeString(strLinesRemoveEmpty(this._))
  }
  public linesTrimLeft(): BemojeString {
    return new BemojeString(strLinesTrimLeft(this._))
  }
  public linesTrimRight(): BemojeString {
    return new BemojeString(strLinesTrimRight(this._))
  }
  public prettifyMinifiedCode(indent = ''): BemojeString {
    return new BemojeString(strPrettifyMinifiedCode(this._, indent))
  }
  public removeDuplicateChars(): BemojeString {
    return new BemojeString(strRemoveDuplicateChars(this._))
  }
  public sortChars(): BemojeString {
    return new BemojeString(strSortChars(this._))
  }
  public splitCamelCase(): Array<string> {
    return strSplitCamelCase(this._)
  }
  public toCharCodes(): Array<number> {
    return strToCharCodes(this._)
  }
  public toCharSet(): BemojeString {
    return new BemojeString(strToCharSet(this._))
  }
  public toSentences(): Array<string> {
    return strToSentences(this._)
  }
  public toWords(): Array<string> {
    return strToWords(this._)
  }
  public unwrap(left: string, right: string, flags?: string): BemojeString {
    return new BemojeString(strUnwrap(this._, left, right, flags))
  }
  public wrapBetween(left: string, right: string): BemojeString {
    return new BemojeString(strWrapBetween(this._, left, right))
  }
  public wrapIn(wrap: string): BemojeString {
    return new BemojeString(strWrapIn(this._, wrap))
  }
  public wrapInAngleBrackets(): BemojeString {
    return new BemojeString(strWrapInAngleBrackets(this._))
  }
  public wrapInBraces(): BemojeString {
    return new BemojeString(strWrapInBraces(this._))
  }
  public wrapInBrackets(): BemojeString {
    return new BemojeString(strWrapInBrackets(this._))
  }
  public wrapInDoubleQuotes(): BemojeString {
    return new BemojeString(strWrapInDoubleQuotes(this._))
  }
  public wrapInParenthesis(): BemojeString {
    return new BemojeString(strWrapInParenthesis(this._))
  }
  public wrapInSingleQuotes(): BemojeString {
    return new BemojeString(strWrapInSingleQuotes(this._))
  }
}

